// ArticleForm.vue
<template>
  <div class="article-form-container">
    <h1 class="page-title">Create New Article</h1>

    <form @submit.prevent="handleSubmit" class="article-form">
      <!-- Title Input -->
      <div class="form-group">
        <label for="title">Article Title</label>
        <input
          id="title"
          v-model="article.title"
          type="text"
          placeholder="Enter article title"
          required
          class="form-input"
        >
      </div>

      <!-- Preview Text -->
      <div class="form-group">
        <label for="preview">Preview Text</label>
        <textarea
          id="preview"
          v-model="article.preview"
          placeholder="Enter a short preview of your article"
          rows="3"
          class="form-input"
        ></textarea>
      </div>

      <!-- Article Content -->
      <div class="form-group">
        <label for="content">Article Content</label>
        <div class="editor-toolbar">
          <button 
            v-for="command in editorCommands" 
            :key="command.id"
            type="button"
            @click="executeCommand(command.command, command.value)"
            class="toolbar-button"
            :title="command.label"
          >
            {{ command.icon }}
          </button>
        </div>
        <div
          id="content"
          :contenteditable="true"
          @input="handleContentInput"
          class="content-editor"
          ref="contentEditor"
        ></div>
      </div>

      <!-- Image Upload -->
      <div class="form-group">
        <label for="image">Featured Image</label>
        <div class="image-upload">
          <input
            id="image"
            type="file"
            @change="handleImageUpload"
            accept="image/*"
            class="hidden"
            ref="imageInput"
          >
          <div 
            class="upload-area"
            @click="triggerImageUpload"
            :class="{ 'has-image': article.image }"
          >
            <img 
              v-if="article.image" 
              :src="article.image" 
              alt="Featured image preview"
              class="image-preview"
            >
            <div v-else class="upload-placeholder">
              <span class="upload-icon">📷</span>
              <span>Click to upload image</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Author Info -->
      <div class="form-group">
        <label for="author">Author Information</label>
        <div class="author-info">
          <div class="author-avatar">
            <img 
              :src="article.author.avatar || '/default-avatar.png'" 
              alt="Author avatar"
            >
          </div>
          <div class="author-details">
            <input
              type="text"
              v-model="article.author.name"
              placeholder="Author name"
              class="form-input"
            >
          </div>
        </div>
      </div>

      <!-- Submit Button -->
      <div class="form-actions">
        <button 
          type="button" 
          @click="previewArticle" 
          class="preview-button"
        >
          Preview
        </button>
        <button 
          type="submit" 
          class="submit-button"
          :disabled="isSubmitting"
        >
          {{ isSubmitting ? 'Publishing...' : 'Publish Article' }}
        </button>
      </div>
    </form>

    <!-- Preview Modal -->
    <div v-if="showPreview" class="preview-modal">
      <div class="preview-content">
        <button @click="showPreview = false" class="close-preview">×</button>
        <h2>{{ article.title }}</h2>
        <img 
          v-if="article.image" 
          :src="article.image" 
          :alt="article.title"
          class="preview-image"
        >
        <div v-html="article.content" class="preview-body"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { articleService } from '@/services/articleService'

export default {
  name: 'ArticleForm',
  
  setup() {
    const router = useRouter()
    const contentEditor = ref(null)
    const imageInput = ref(null)
    const showPreview = ref(false)
    const isSubmitting = ref(false)
    const imageFile = ref(null)

    const article = reactive({
      title: '',
      preview: '',
      content: '',
      image: null,
      imageUrl: null,
      author: {
        name: '',
        avatar: '/default-avatar.png'
      },
      publishedAt: new Date()
    })

    const handleImageUpload = (event) => {
      const file = event.target.files[0]
      if (file) {
        imageFile.value = file
        const reader = new FileReader()
        reader.onload = (e) => {
          article.image = e.target.result // For preview
        }
        reader.readAsDataURL(file)
      }
    }

    const handleSubmit = async () => {
      try {
        isSubmitting.value = true

        // Upload image first if exists
        if (imageFile.value) {
          article.imageUrl = await articleService.uploadImage(imageFile.value)
        }

        // Create article with image URL
        const articleData = {
          title: article.title,
          preview: article.preview,
          content: article.content,
          imageUrl: article.imageUrl,
          author: article.author,
          publishedAt: article.publishedAt
        }

        const articleId = await articleService.createArticle(articleData)
        router.push(`/blog/${articleId}`)
      } catch (error) {
        console.error('Error publishing article:', error)
        alert('Failed to publish article. Please try again.')
      } finally {
        isSubmitting.value = false
      }
    }


    const editorCommands = [
      { id: 1, label: 'Bold', icon: 'B', command: 'bold' },
      { id: 2, label: 'Italic', icon: 'I', command: 'italic' },
      { id: 3, label: 'Heading 2', icon: 'H2', command: 'formatBlock', value: 'h2' },
      { id: 4, label: 'Heading 3', icon: 'H3', command: 'formatBlock', value: 'h3' },
      { id: 5, label: 'Quote', icon: '"', command: 'formatBlock', value: 'blockquote' },
      { id: 6, label: 'Code', icon: '<>', command: 'formatBlock', value: 'pre' },
      { id: 7, label: 'Link', icon: '🔗', command: 'createLink' }
    ]

    const executeCommand = (command, value) => {
      if (command === 'createLink') {
        const url = prompt('Enter URL:')
        if (url) document.execCommand(command, false, url)
      } else {
        document.execCommand(command, false, value)
      }
      contentEditor.value.focus()
    }

    const handleContentInput = (event) => {
      article.content = event.target.innerHTML
    }


    const triggerImageUpload = () => {
      imageInput.value.click()
    }

    const previewArticle = () => {
      showPreview.value = true
    }

    onMounted(() => {
      if (contentEditor.value) {
        contentEditor.value.innerHTML = article.content
      }
    })

    return {
      article,
      contentEditor,
      imageInput,
      showPreview,
      isSubmitting,
      editorCommands,
      executeCommand,
      handleContentInput,
      handleImageUpload,
      triggerImageUpload,
      previewArticle,
      handleSubmit
    }
  }
}
</script>

