// ArticleDetail.vue
<template>
  <div class="article-detail" v-if="article">
    <div class="article-header">
      <img 
        :src="article.imageUrl" 
        :alt="article.title"
        class="article-hero-image"
      >
      <div class="article-header-content">
        <h1 class="article-title">{{ article.title }}</h1>
        <div class="article-meta">
          <div class="author-info">
            <!-- <img 
              :src="article.author.avatar" 
              :alt="article.author.name"
              class="author-avatar"
            > -->
            <div class="author-details">
              <span class="author-name">{{ article.author.name }}</span>
              <span class="article-date">{{ formatDate(article.publishedAt) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="article-content">
      <div v-html="article.content"></div>
    </div>

    <div class="article-footer">
      <button 
        @click="goBack" 
        class="back-button"
      >
        ← Back to Articles
      </button>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { articleService } from '@/services/articleService'
import { formatDate } from '@/utils/dateUtils'

export default {
  name: 'ArticleDetail',
  
  setup() {
    const route = useRoute()
    const router = useRouter()
    const article = ref(null)
    const loading = ref(true)

    const fetchArticle = async (id) => {
      try {
        loading.value = true
        article.value = await articleService.getArticle(id)
        
        if (!article.value) {
          router.push('/articles') // Redirect if article not found
        }
      } catch (error) {
        console.error('Error fetching article:', error)
        alert('Failed to load article. Please try again.')
      } finally {
        loading.value = false
      }
    }

    const goBack = () => {
      router.push('/blog')
    }

    onMounted(async () => {
      const articleId = route.params.id
      await fetchArticle(articleId)
    })

    return {
      article,
      loading,
      formatDate,
      goBack
    }
  }
}
</script>

